<template>
  <div class="page settings-page">
    <w-header v-if="isHeader">
      <span class="logo">W</span>
      <button @click="showNav"><i class="fas fa-bars fa-2x"></i></button>
    </w-header>
    <div class="text-container">
      <h1>Welcome to the settings page !</h1>
      <p>
        Here you can set your interests in people so we can match you with
        people who share the same interests with you !
        <br />
        <span
          >Note: if you take a lot of time to match with people, try reducing
          the filters used.</span
        >
      </p>
      <div class="interests">
        <input type="checkbox" id="match-everyone" name="match-everyone" />
        <label for="match-everyone">Match me with anyone</label>
        <div>
          <h3>I want to be matched with :</h3>
          <div class="tags-container">
            <input type="radio" id="male-tag" name="gender" />
            <label for="male-tag">Males</label>
            <input type="radio" id="female-tag" name="gender" />
            <label for="female-tag">Females</label>
            <input type="radio" id="any-gender" name="gender" />
            <label for="any-gender">Any</label>
          </div>
        </div>
        <div>
          <h3>I want to be matched with people from :</h3>
          <div class="tags-container">
            <input type="radio" name="country" id="my-country-tag" />
            <label for="my-country-tag">My country</label>
            <input type="radio" name="country" id="any-country-tag" />
            <label for="any-country-tag">Any country</label>
          </div>
        </div>
        <div>
          <h3>My interests :</h3>
          <div class="tags-container">
            <input type="checkbox" id="sports" />
            <label for="sports">sports</label>

            <input type="checkbox" id="music" />
            <label for="music">music</label>

            <input type="checkbox" id="technology" />
            <label for="technology">technology</label>

            <input type="checkbox" id="business" />
            <label for="business">business</label>

            <input type="checkbox" id="travelling" />
            <label for="travelling">travelling</label>

            <input type="checkbox" id="memes" />
            <label for="memes">memes</label>

            <input type="checkbox" id="anime" />
            <label for="anime">anime</label>

            <input type="checkbox" id="society" />
            <label for="society">society</label>

            <input type="checkbox" id="gaming" />
            <label for="gaming">gaming</label>

            <input type="checkbox" id="movies" />
            <label for="movies">movies</label>

            <input type="checkbox" id="genre-drama" />
            <label for="genre-drama" class="movie-genre">drama</label>

            <input type="checkbox" id="genre-action" />
            <label for="genre-action" class="movie-genre">action</label>

            <input type="checkbox" id="genre-horror" />
            <label for="genre-horror" class="movie-genre">horror</label>

            <input type="checkbox" id="genre-comedy" />
            <label for="genre-comedy" class="movie-genre">comedy</label>

            <input type="checkbox" id="genre-superhero" />
            <label for="genre-superhero" class="movie-genre">superhero</label>
          </div>
        </div>
        <button class="save" @click="updateUserPrefs">save changes</button>
      </div>
    </div>
  </div>
</template>

<script>
import WHeader from "../components/WHeader.vue";
export default {
  name: "Settings",
  data() {
    return {
      isHeader: false,
    };
  },
  components: { WHeader },
  computed: {
    thisUser() {
      return this.$store.state.user;
    },
  },
  beforeMount() {
    if (this.thisUser.name === "") {
      document.getElementById("home-link").click();
    }
  },
  methods: {
    showNav() {
      this.$store.commit("toggleNav");
    },
    updateUserPrefs() {
      let gender;
      if (document.getElementById("male-tag").checked) {
        gender = "male";
      } else if (document.getElementById("female-tag").checked) {
        gender = "female";
      } else {
        gender = "any";
      }

      let country;
      if (document.getElementById("my-country-tag").checked) {
        country = this.thisUser.details.country;
      } else {
        country = "any";
      }

      let sports = document.getElementById("sports").checked;
      let music = document.getElementById("music").checked;
      let technology = document.getElementById("technology").checked;
      let travelling = document.getElementById("travelling").checked;
      let memes = document.getElementById("memes").checked;
      let anime = document.getElementById("anime").checked;
      let drama = document.getElementById("genre-drama").checked;
      let action = document.getElementById("genre-action").checked;
      let horror = document.getElementById("genre-horror").checked;
      let comedy = document.getElementById("genre-comedy").checked;
      let superhero = document.getElementById("genre-superhero").checked;

      let matchEveryOneStatus =
        document.getElementById("match-everyone").checked;

      let userPrefs = {
        gender,
        country,
      };
      let userInterests = {
        sports,
        music,
        technology,
        travelling,
        memes,
        anime,
        movies: {
          drama,
          action,
          horror,
          comedy,
          superhero,
        },
      };
      this.$store.commit("setUserPrefs", {
        userPrefs,
        userInterests,
        matchEveryOneStatus,
      });
      this.isHeader = true;
      window.scrollTo(0, 0);
      document.getElementById("home-link").click();
    },
  },
};
</script>

<style lang="scss" scoped>
h1 {
  margin-top: 60px;
}
.interests {
  margin: 60px 0;
  text-align: left;

  button.save {
    border-radius: 40px;
    padding: 10px 20px;
    background-color: var(--accent);
    color: white;
    margin: 0 10px 20px;
  }

  .tags-container {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
  }

  & > div {
    margin: 40px 0;
  }

  h3 {
    margin: 40px 0;
  }

  input[type="radio"],
  input[type="checkbox"] {
    display: none;
    &:checked + label {
      background-color: var(--accent);
      color: white;
    }
    &:checked#movies ~ .movie-genre {
      display: inline-block;
    }
  }

  label {
    margin: 10px;
    padding: 10px;
    font-weight: bold;
    color: var(--accent);
    border: 2px solid var(--accent);
    border-radius: 40px;
    font-size: 0.9em;
    cursor: pointer;
    &.movie-genre {
      display: none;
    }
  }
}
</style>
